html {
  height: 100%;
}
h1,
h2,
h3,
h4 {
  font-family: 'Dancing Script';
}
p,
span {
  font-family: 'Quicksand';
}
body {
  background: url(../img/texture.jpg);
  height: 100%;
}
.no-pad {
  padding: 0;
}
header {
  display: none;
}
header .container {
  width: 100%;
}
header .navbar {
  box-shadow: 7px 7px 0px 0px rgba(50, 50, 50, 0.75);
  font-family: 'Quicksand';
  font-size: 20px;
  padding: 10px 0;
  margin: 0;
  text-transform: uppercase;
  background: rgba(250, 250, 250, 0.8);
}
header .navbar .navbar-brand {
  position: absolute;
  left: 50%;
  transform: translate(-50%, 0);
  text-align: center;
  text-indent: -9999px;
  background: url(../img/logo-navbar.png) no-repeat center center;
  width: 219px;
  height: 55px;
  margin: 0 !important;
}
header .navbar li a {
  padding: 20px 50px;
  font-weight: 700;
  text-align: center;
  color: #000 !important;
}
header .navbar li a:hover {
  color: #638f94 !important;
}
header .navbar li a:focus {
  outline: none;
}
.wrap {
  position: relative;
  width: 100%;
  text-align: center;
  height: 100%;
}
.wrap #scene {
  overflow: hidden;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  position: fixed;
  z-index: -1;
}
.wrap .layer {
  width: 100%;
  height: 100%;
}
.wrap .layer:nth-child(3) {
  z-index: 2;
}
.wrap .layer .cloud {
  background: url(../img/cloud.png) no-repeat center center;
  position: absolute;
  width: 701px;
  height: 967px;
  top: -200px;
  left: -50px;
}
.wrap .layer .cloud-1 {
  background: url(../img/cloud-1.png) no-repeat center center;
  position: absolute;
  width: 320px;
  height: 441px;
  top: -150px;
  left: 300px;
}
.wrap .layer .cloud-2 {
  background: url(../img/cloud-2.png) no-repeat center center;
  position: absolute;
  width: 500px;
  height: 689px;
  top: -280px;
  right: 50px;
}
.wrap .layer .cloud-3 {
  background: url(../img/cloud-3.png) no-repeat center center;
  position: absolute;
  width: 250px;
  height: 345px;
  top: 0px;
  right: 50px;
  left: auto;
}
.wrap .welcome-container {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100%;
  transform: translate(-50%, -50%);
}
.wrap .welcome-container h1.logo {
  text-indent: -9999px;
  background: url(../img/logo.png) no-repeat center center;
  width: 324px;
  height: 197px;
  margin: 80px auto;
}
.wrap .welcome-container h2.slogan {
  font-size: 40px;
  color: #fff;
  margin: 40px auto;
}
.wrap .welcome-container .arrow {
  border-radius: 50%;
  display: inline-block;
  width: 90px;
  height: 90px;
  background: rgba(255, 255, 255, 0.5);
  margin-top: 50px;
}
.wrap .welcome-container .arrow img {
  margin-top: 22px;
}
footer {
  padding: 60px 0 0 0;
  overflow: hidden;
}
footer .footer {
  padding: 0 0 60px 0;
}
footer h3 {
  text-align: center;
  font-size: 42px;
  color: #fff;
  margin: 20px auto 40px auto;
  text-shadow: 2px 2px 2px #323232;
  position: relative;
  z-index: 2;
}
footer h3:after {
  position: absolute;
  content: '';
  top: -144px;
  left: 53%;
  transform: translate(-50%, 0);
  background: url(../img/contatti-cloud.png) no-repeat center center;
  width: 450px;
  height: 234px;
  z-index: -1;
}
footer .footer-box {
  background: #f6f0e5;
  padding: 20px;
  box-shadow: 7px 7px 0px 0px rgba(50, 50, 50, 0.75);
}
footer .footer-box h4 {
  font-family: 'Quicksand';
  font-weight: 700;
  text-transform: uppercase;
  padding: 5px 0;
  margin: 0 0 35px 0;
  border-bottom: 1px solid #000;
}
footer .footer-box.info ul {
  padding: 0;
  list-style: none;
  font-family: 'Quicksand';
}
footer .footer-box.info ul li {
  margin: 15px 0;
  color: #000;
  font-size: 16px;
}
footer .footer-box.info ul li i {
  font-size: 16px;
  margin-right: 10px;
  width: 20px;
}
footer .footer-box.info a {
  color: #000;
}
footer .footer-box.social {
  font-size: 16px;
}
footer .footer-box.social p {
  margin: 15px 0 25px 0;
}
footer .footer-box.social a:hover {
  text-decoration: none;
}
footer .footer-box.social .facebook {
  display: inline-block;
  background: url(../img/social-footer.png) no-repeat;
  width: 43px;
  height: 43px;
  margin-right: 15px;
  background-position: top left;
}
footer .footer-box.social .instagram {
  display: inline-block;
  background: url(../img/social-footer.png) no-repeat;
  width: 43px;
  height: 43px;
  background-position: top right;
}
footer .container-fluid {
  padding: 0;
}
footer .partners {
  padding: 60px 0;
  margin: 40px 0 0 0;
  background: #f6f0e5;
}
footer .partners div {
  outline: none;
}
footer .partners img {
  text-align: center;
  margin: auto;
}
footer .copyright {
  margin: 0;
}
footer .copyright p {
  margin: 0;
  padding: 10px;
  color: #fff;
}
footer .copyright a {
  color: #fff;
}
footer .copyright a:hover {
  color: #fff;
}
.white-popup {
  position: relative;
  background: #FFF;
  padding: 20px;
  width: auto;
  max-width: 500px;
  margin: 20px auto;
}
.white-popup img {
  display: block;
  margin: 10px auto 40px auto;
}
.white-popup p {
  text-align: justify;
}
section.main {
  margin-top: 30px;
}
section.main h3 {
  text-align: center;
  position: relative;
  font-size: 50px;
  color: #fff;
  margin: 20px auto 40px auto;
  text-shadow: 2px 2px 2px #323232;
  border-radius: 30px;
  background: #638f94;
  width: 230px;
  padding: 10px;
  box-shadow: 0px 8px 0px 0px rgba(0, 0, 0, 0.6);
}
section.main h3:after {
  border: 1px solid #fff;
  position: absolute;
  content: '';
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 93%;
  height: 85%;
  border-radius: 25px;
}
section.main h3.beige {
  color: #638f94;
  background: #f6f0e5;
}
section.main h3.beige:after {
  border-color: #638f94;
}
.opening-carousel .slide {
  background: url(http://placehold.it/1200x500) no-repeat center center;
  width: 100%;
  height: 500px;
  background-size: cover;
}
.opening-carousel .slick-prev {
  left: 50px;
  background: url(../img/arrow.png) no-repeat center center;
  width: 81px;
  height: 43px;
  z-index: 5;
  transform: rotate(90deg);
}
.opening-carousel .slick-prev:before {
  display: none;
}
.opening-carousel .slick-next {
  right: 50px;
  background: url(../img/arrow.png) no-repeat center center;
  width: 81px;
  height: 43px;
  z-index: 5;
  transform: rotate(-90deg);
}
.opening-carousel .slick-next:before {
  display: none;
}
#about {
  padding: 30px 0;
}
.about-us {
  padding: 40px;
  min-height: 590px;
  background: #f6f0e5;
  margin: -50px 0;
  position: relative;
  box-shadow: -15px 15px 0px 0px rgba(0, 0, 0, 0.6);
}
.about-us p {
  color: #000;
  font-size: 22px;
  text-align: justify;
}
section.who {
  padding: 120px 0;
  box-shadow: 0px 8px 0px 0px rgba(180, 180, 180, 0.75);
  position: relative;
  z-index: 2;
}
section.who .who-box {
  margin: 20px auto;
  text-align: center;
}
section.who .who-box img {
  border-radius: 50%;
  margin-bottom: 40px;
}
section.who .who-box h4 {
  font-size: 38px;
  color: #fff;
}
section.who .who-box p {
  font-size: 22px;
  color: #fff;
}
section.who .who-box .icons {
  margin: 25px auto;
}
section.who .who-box .icons div:nth-child(2) {
  margin: 0 35px;
}
section.who .who-box .icons .facebook {
  display: inline-block;
  background: url(../img/icons.png) no-repeat;
  width: 33px;
  height: 33px;
  background-position: left top;
}
section.who .who-box .icons .instagram {
  display: inline-block;
  background: url(../img/icons.png) no-repeat;
  width: 33px;
  height: 33px;
  background-position: 50% 0px;
}
section.who .who-box .icons .mail {
  display: inline-block;
  background: url(../img/icons.png) no-repeat;
  width: 33px;
  height: 33px;
  background-position: right top;
}
section.events {
  padding: 120px 0;
  background: #f6f0e5;
  box-shadow: 0px 8px 0px 0px rgba(180, 180, 180, 0.75);
  z-index: 2;
}
section.events .eventi {
  box-shadow: 16px 16px 0px 0px rgba(180, 180, 180, 0.75);
  padding: 40px;
  min-height: 590px;
  background: #638f94;
  margin: -30px 0;
  position: relative;
  z-index: 2;
}
section.events .eventi .row {
  border-top: 2px solid #fff;
  border-bottom: 2px solid #fff;
}
section.events .eventi ul {
  list-style: none;
  padding: 0;
  text-align: center;
}
section.events .eventi ul li {
  color: #000;
  font-family: 'Quicksand';
  font-size: 28px;
  margin: 20px 0;
}
section.events .servizi {
  margin-top: 0px;
}
section.events .servizi .col-lg-2:nth-child(2) .service-box,
section.events .servizi .col-lg-2:nth-child(4) .service-box,
section.events .servizi .col-lg-2:nth-child(6) .service-box,
section.events .servizi .col-lg-2:nth-child(9) .service-box,
section.events .servizi .col-lg-2:nth-child(11) .service-box,
section.events .servizi .col-lg-2:nth-child(13) .service-box {
  background: #638f94;
}
section.events .servizi .service-box {
  text-align: center;
  padding: 20px;
  background: #c1d2d4;
  height: 210px;
}
section.events .servizi .service-box .icon {
  margin-bottom: 20px;
}
section.events .servizi .service-box .icon img {
  margin: auto;
  margin-bottom: 20px;
}
section.events .servizi .service-box p {
  font-size: 20px;
  margin-top: 20px;
}
section.gallery {
  margin: 0 5px;
}
section.gallery h3 {
  margin: 80px auto 50px auto;
}
section.gallery .project {
  position: relative;
  margin: 10px;
  overflow: hidden;
  height: 100%;
  box-shadow: 10px 10px 0px 0px rgba(50, 50, 50, 0.75);
  transform: translate3d(0, 0, 0);
}
section.gallery .project:before {
  position: absolute;
  content: '';
  left: 0;
  top: 0;
  background: url(../img/left-tent.png) no-repeat center center;
  width: 30%;
  height: 100%;
  background-size: cover;
  transition: 0.35s;
  z-index: 2;
  transform: translate3d(0, 0, 0);
}
section.gallery .project:after {
  position: absolute;
  content: '';
  right: 0;
  top: 0;
  background: url(../img/right-tent.png) no-repeat center center;
  width: 30%;
  height: 100%;
  background-size: cover;
  transition: 0.35s;
  z-index: 2;
  transform: translate3d(0, 0, 0);
}
section.gallery .project:hover:before {
  left: -160px;
  transition: 0.35s;
}
section.gallery .project:hover:after {
  right: -160px;
  transition: 0.35s;
}
section.gallery .project:hover .project-img {
  filter: grayscale(0);
  transition: 0.35s;
  transform: scale(1.1);
}
section.gallery .project .project-desc {
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: 10;
  padding: 10px;
  text-align: center;
  width: 100%;
  background: #638f94;
}
section.gallery .project .project-desc p {
  font-size: 30px;
  color: #fff;
  margin: 0;
}
section.gallery .project .project-img {
  width: 100%;
  filter: grayscale(100%);
  transition: 0.35s;
}
@media screen and (min-width: 1199px) and (max-width: 1500px) {
  section.events .eventi ul li {
    font-size: 18px;
  }
  section.events .servizi .service-box p {
    font-size: 16px;
  }
}
@media screen and (max-width: 1600px) {
  header .container {
    width: 100%;
  }
  header .navbar li a {
    padding: 15px 30px;
    font-size: 16px;
  }
  .about-us {
    padding: 15px;
  }
}
@media screen and (min-width: 2000px) and (max-width: 2800px) {
  .wrap .layer .cloud {
    left: -50px;
