  }
}
@media screen and (min-width: 768px) and (max-width: 991px) {
  .wrap .layer .cloud {
    transform: scale(0.75);
    left: -100px;
  }
  .wrap .layer .cloud-1 {
    transform: scale(0.75);
    left: 100px;
  }
  .wrap .layer .cloud-2 {
    transform: scale(0.75);
    right: -50px;
  }
  .wrap .layer .cloud-3 {
    transform: scale(0.75);
    right: -50px;
  }
  .about-us {
    min-height: auto;
    padding: 40px;
  }
  section.events {
    padding: 0;
  }
  section.events .servizi {
    margin-top: 100px;
  }
  section.events .servizi .col-lg-2:nth-child(2) .service-box,
  section.events .servizi .col-lg-2:nth-child(5) .service-box,
  section.events .servizi .col-lg-2:nth-child(6) .service-box,
  section.events .servizi .col-lg-2:nth-child(9) .service-box,
  section.events .servizi .col-lg-2:nth-child(10) .service-box,
