    background: #638f94;
  }
  section.events .servizi .col-lg-2:nth-child(3) .service-box,
  section.events .servizi .col-lg-2:nth-child(4) .service-box,
  section.events .servizi .col-lg-2:nth-child(7) .service-box,
  section.events .servizi .col-lg-2:nth-child(8) .service-box,
  section.events .servizi .col-lg-2:nth-child(11) .service-box,
  section.events .servizi .col-lg-2:nth-child(12) .service-box {
    background: #c1d2d4;
  }
  footer .footer-box {
    margin: 20px auto;
  }
}
@media screen and (max-width: 767px) {
  .wrap .welcome-container h2.slogan {
    font-size: 24px;
    padding: 0 5px;
    margin: 10px;
  }
  .wrap .welcome-container h1.logo {
    transform: scale(0.7);
    margin: 10px auto;
  }
  .wrap .welcome-container .arrow {
    transform: scale(0.5);
  }
  .wrap .layer .cloud {
    display: none;
  }
  .wrap .layer .cloud-1 {
    transform: scale(0.75);
    left: -100px;
    top: -250px;
  }
  .wrap .layer .cloud-2 {
    display: none;
  }
  .wrap .layer .cloud-3 {
    transform: scale(0.75);
    right: -50px;
    top: -50px;
  }
  .wrap .centered h3 {
    font-size: 20px;
    padding: 10px;
  }
  .wrap .centered p.info {
    font-size: 18px;
  }
  .about-us {
    min-height: auto;
  }
  .about-us p {
    font-size: 18px;
  }
  section.events {
    padding: 0;
  }
  section.events .eventi {
    margin: 0;
