  .opening-carousel .slick-next {
    display: none !important;
  }
  .opening-carousel .slick-prev {
    display: none !important;
  }
  .opening-carousel .slide {
    height: 350px;
  }
  footer .footer-box {
    margin: 20px auto;
  }
  footer .partners {
    margin-top: 0;
  }
  footer .copyright {
    text-align: center;
  }
  footer .copyright p.pull-right {
    float: none !important;
  }
}
@media screen and (max-width: 1199px) {
  header .navbar .navbar-brand {
    margin-top: -5px !important;
  }
  .navbar-header {
    float: none;
  }
  .nav {
    text-align: center;
    margin-right: 0 !important;
  }
  .navbar-left,
  .navbar-right {
    float: none !important;
  }
  .navbar-toggle {
    display: block;
    border: 0;
    border-radius: 0;
  }
  .navbar-toggle .icon-bar {
    background: #638f94 !important;
  }
  .navbar-toggle:hover {
    background: transparent !important;
  }
  .navbar-toggle:focus {
    background: transparent !important;
  }
  .navbar-collapse {
    margin-top: 40px;
    box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.1);
  }
  .navbar-fixed-top {
    top: 0;
    border-width: 0 0 1px;
  }
  .navbar-collapse.collapse {
    display: none!important;
  }
  .navbar-nav {
    float: none!important;
    margin-top: 7.5px;
  }
  .navbar-nav > li {
    float: none;
  }
  .navbar-nav > li > a {
    padding-top: 10px;
    padding-bottom: 10px;
  }
  .collapse.in {
    display: block !important;
  }
}
